.skills__container{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 2rem;
}

#skills{
    margin-bottom: 2rem;
}

.skills__group {
    background: var(--color-bg-variant);
    padding: 2rem 2.5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.skills__group:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.skills__group h3{
    text-align: center;
    margin-bottom: 1.5rem;
    color: var(--color-primary);
    font-size: 1.2rem;
}

.skills__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
}

.skills__details{
    display: flex;
    align-items: center;
    gap: 0.8rem;
    background: rgba(255,255,255,0.05);
    padding: 0.5rem 1rem;
    border-radius: 1rem;
    transition: background 0.2s ease;
}

.skills__details:hover {
    background: rgba(255,255,255,0.12);
}

.skills__details-icon{
    color: var(--color-primary);
    font-size: 1.1rem;
}

/* Medium devices */
@media screen and (max-width:1024px){
    .skills__container{
        grid-template-columns: 1fr;
    }

    .skills__group{
        width: 80%;
        margin: 0 auto;
        padding: 2rem;
    }

    .skills__content{
        padding: 0.5rem 0;
        grid-template-columns: 1fr 1fr;
    }
}

/* Small devices */
@media screen and (max-width:600px){
    .skills__container{
        gap: 1rem;
    }

    .skills__group{
        width: 100%;
        padding: 1.5rem;
    }

    .skills__content{
        grid-template-columns: 1fr;
        gap: 0.8rem;
    }
}
