/* Header */
header {
    min-height: 100vh;
    padding: 7rem 2rem 2rem; /* top padding for header text, bottom padding optional */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* centers all header content vertically */
    position: relative;
    overflow: visible;
}

/* Header container */
.header__container{
    text-align: center;
    max-width: 1200px;
    width: 100%;
}

/* CTA buttons */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* Header socials */
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:1.5rem;
    position: absolute;
    left:2rem;
    bottom: 5cap;
}


.me {
    width: 20vw;
    max-width: 22rem;         
    height: 20vw;
    max-height: 22rem; 
    border-radius: 50%;
    overflow: hidden;
    margin: 2rem auto 0;
    padding: 0;
    background: radial-gradient(circle, var(--color-primary) 0%, #1f1f38 70%);
    display: flex;
    align-items: center;
    justify-content: center;
}


/* Scroll Down */
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

.rotating-text {
  font-weight: 700;
  color: var(--color-primary);
}  


/* Media queries (Medium devices) */
@media screen and (max-width:1024px){
    .me{
        width: 18rem;
        height: 24rem;
    }
}

@media screen and (max-width:600px){
    .me{
        width: 16rem;
        height: 20rem;
    }
}