/* About image container */
.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 50%;
    display: grid;
    place-items: center;
    overflow: hidden;
    margin: 0 auto;
    box-shadow: 0 8px 20px rgba(0,0,0,0.5); 
    background: rgba(255,255,255,0.05); 
    transition: transform 0.4s ease;
}

.about__me:hover {
    transform: scale(1.05);}

.about__me-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    transition: transform 0.4s ease;
}

.about__me-image:hover img {
    transform: scale(1.05);         /* zoom effect on image itself */
}


.about__cards{
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* fixed typo "repear" */
    gap: 1.5rem;
}

.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.95rem;
}

.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* Media queries (Medium devices) */
@media screen and (max-width:1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me{
        width: 50%;
        margin: 2rem auto 3rem; /* increased top margin to avoid header overlap */
    }

    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}

/* Media queries (Small devices) */
@media screen and (max-width:600px){
    .about__me{
        width: 65%;
        margin: 2rem auto 2rem; /* ensure enough top space below header image */
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;
    }
}
