#education {
  padding: 3rem 0;
  text-align: center;
  color: var(--color-text);
  margin-top: 3rem; 
}

#education h5 {
  color: var(--color-light);
  font-weight: 400;
}

#education h2 {
  margin-bottom: 3rem;
  color: var(--color-primary);
}

.education__container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 900px;
  margin: 0 auto;
  position: relative;
}

.education__item {
  background: var(--color-bg-variant);
  border-left: 4px solid var(--color-primary);
  padding: 1.8rem 2rem;
  border-radius: 1rem;
  text-align: left;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
}

.education__item:hover {
  transform: translateY(-5px);
  border-left-color: var(--color-primary-variant);
  background: var(--color-bg);
}

.education__item h3 {
  color: var(--color-primary);
  font-size: 1.1rem;
  margin-bottom: 0.3rem;
}

.education__item h4 {
  font-weight: 500;
  color: var(--color-light);
  font-size: 0.95rem;
  margin-bottom: 0.5rem;
}

.education__date {
  font-size: 0.9rem;
  color: var(--color-primary-variant);
  font-weight: 600;
  margin-bottom: 0.6rem;
}

.education__details {
  font-size: 0.9rem;
  line-height: 1.6;
  color: var(--color-text);
}

@media screen and (max-width: 768px) {
  .education__container {
    padding: 0 1.5rem;
  }

  .education__item {
    padding: 1.5rem;
  }

  .education__item h3 {
    font-size: 1rem;
  }
}
